import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators
} from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-admin-user',
  templateUrl: './admin-user.component.html',
  styleUrls: ['./admin-user.component.css']
})
export class AdminUserComponent implements OnInit {
  api_url = '';
  queryParams = {}
  data = [];

  searchForm: FormGroup;
  addForm: FormGroup;
  isVisible = false;
  isConfirmLoading = false;
  isEditing = false;

  constructor(private fb: FormBuilder,
              private http: HttpClient)
  {

  }

  ngOnInit() {
    this.searchForm = this.fb.group({});
    this.searchForm.addControl(`account`, new FormControl());
    this.searchForm.addControl(`role`, new FormControl());
    this.searchForm.addControl(`status`, new FormControl());

    this._initAddForm()
    this.getList()
  }

  _initAddForm():void {
    this.addForm = this.fb.group({
      id               : [ null, [ Validators.required ] ],
      account          : [ null, [ Validators.required ] ],
      email            : [ null, [ Validators.email ] ],
      password         : [ null, [ Validators.required ] ],
      role             : [ null, [ Validators.required ] ]
    });
  }

  /**这里的账号列表相关 */
  getList():void {
    let params = this.queryParams;
    let query =  Object.keys(params).map(function(key) {
      if (params[key] !== null) {
        return key + '=' + params[key];
      }
    }).join('&');
    console.log(query);
    this.http
      .get(this.api_url + '/api/admin/user/get-list/?'+ query)
      .subscribe(
          data => {
            if (data['c'] == 0) {
              this.data = data['d']['data'];
            }
          },
          err => {
              alert('查询失败，请重试');
          }
      );
  }

  search():void {
    for (const i in this.searchForm.controls) {
      this.queryParams[i] = this.searchForm.controls[ i ].value;
    }
    this.getList();
  }

  resetForm():void {
    this.searchForm.reset();
    this.queryParams = {};
  }

  openEditModal(user):void {
    console.log('edit', user);
    this.isVisible = true;
    this.isEditing = true;
    for (const i in this.addForm.controls) {
      if (i !== 'password') {
        this.addForm.controls[ i ].patchValue(user[i]);
      }
    }
  }

  delete(id):void {
    console.log('delete', id);
    let params = { id: id, status: 0 } 
    this._update(id, params);
  }

  recover(id):void {
    console.log('recover', id);
    let params = { id: id, status: 1 } 
    this._update(id, params);
  }

  _update(id, params):void{
    const options: any = { 
      headers: new HttpHeaders({ 'Content-Type': 'application/x-www-form-urlencoded'})
    };
    let body = Object.keys(params).map(function(key) {
      if (params[key] !== null) {
        return key + '=' + params[key];
      }
    }).join('&');
    this.http
      .post(this.api_url + '/api/admin/user/update/', body, options)
      .subscribe(
          data => {
            console.log(data);
            if (data['c'] !== 0) {
              alert(data['m']);
              this.isConfirmLoading = false;
            } else {
              this.isVisible = false;
              this.isConfirmLoading = false;
              this.addForm.reset();
              this.search();
            }
          },
          err => {
              alert('提交失败，请重试！');
              this.isConfirmLoading = false;
          }
      );
  }


  /**这里的加账号相关 */
  add():void {
    this.addForm.reset();
    this.isVisible = true;
  }

  saveAccount = (e) => {
    this.isConfirmLoading = true;
    const options: any = { 
      headers: new HttpHeaders({ 'Content-Type': 'application/x-www-form-urlencoded'})
    };
    let params = {}
    for (const i in this.addForm.controls) {
      params[i] = this.addForm.controls[ i ].value;
    }
    let body = Object.keys(params).map(function(key) {
      if (params[key] !== null) {
        return key + '=' + params[key];
      }
    }).join('&');
    console.log(params);
    let url = this.api_url + '/api/admin/user/new/';
    if (this.isEditing) {
      url = this.api_url + '/api/admin/user/update/';
    }
    console.log(body);
    this.http
      .post(url, body, options)
      .subscribe(
          data => {
            console.log(data);
            if (data['c'] !== 0) {
              alert(data['m']);
              this.isConfirmLoading = false;
            } else {
              this.isVisible = false;
              this.isConfirmLoading = false;
              this.addForm.reset();
              this.search();
            }
          },
          err => {
            alert('提交失败，请重试！');
            this.isConfirmLoading = false;
          }
      );
  }

  handleCancel = (e) => {
    this.isVisible = false;
  }

  _submitForm() {
    for (const i in this.addForm.controls) {
      this.addForm.controls[ i ].markAsDirty();
    }
  }

  getAddFormControl(name) {
    return this.addForm.controls[ name ];
  }

}



