import { NgModule }              from '@angular/core';
import { RouterModule, Routes }  from '@angular/router';
import { AuthGuard } from './auth-guard.service';

import { LoginComponent } from './login/login.component';
import { NotfoundComponent } from './notfound/notfound.component';
import { MainComponent } from './main/main.component';
import { AdminUserComponent } from './main/admin/admin-user/admin-user.component';
import { AdminPrivilegeComponent } from './main/admin/admin-privilege/admin-privilege.component';
import { DashboardComponent } from './main/admin/dashboard/dashboard.component';
import { ProxyUserComponent } from './main/proxy/proxy-user/proxy-user.component';
import { ProxyApikeyComponent } from './main/proxy/proxy-apikey/proxy-apikey.component';
import { ProxyOrderComponent } from './main/proxy/proxy-order/proxy-order.component';
import { ProxyIppoolComponent } from './main/proxy/proxy-ippool/proxy-ippool.component';
import { ProxyCrawlComponent } from './main/proxy/proxy-crawl/proxy-crawl.component';
import { AnalysisDoubanGroupComponent } from './main/analysis/analysis-douban-group/analysis-douban-group.component';
import { AnalysisLagouJobComponent } from './main/analysis/analysis-lagou-job/analysis-lagou-job.component';
import { AnalysisZhihuTopicComponent } from './main/analysis/analysis-zhihu-topic/analysis-zhihu-topic.component';


const appRoutes: Routes = [
    { path: "login", component: LoginComponent },
    { path: "notfound", component: NotfoundComponent },
    { path: "", redirectTo: 'layout', pathMatch: 'full' },
    { 
      path: "layout",
      component: MainComponent,
      canActivate: [AuthGuard],
      children: [
        { path: "admin/user", component: AdminUserComponent, canActivate: [AuthGuard] },
        { path: "admin/privilege", component: AdminPrivilegeComponent, canActivate: [AuthGuard] },
        { path: "admin/dashboard", component: DashboardComponent, canActivate: [AuthGuard] },

        { path: "proxy/user", component: ProxyUserComponent, canActivate: [AuthGuard] },
        { path: "proxy/order", component: ProxyOrderComponent, canActivate: [AuthGuard] },
        { path: "proxy/apikey", component: ProxyApikeyComponent, canActivate: [AuthGuard] },
        { path: "proxy/ippool", component: ProxyIppoolComponent, canActivate: [AuthGuard] },
        { path: "proxy/crawl", component: ProxyCrawlComponent, canActivate: [AuthGuard] },
    
        { path: "analysis/douban/group", component: AnalysisDoubanGroupComponent, canActivate: [AuthGuard] },
        { path: "analysis/lagou/job", component: AnalysisLagouJobComponent, canActivate: [AuthGuard] },
        { path: "analysis/zhihu/topic", component: AnalysisZhihuTopicComponent, canActivate: [AuthGuard] },
      ]
    },
];

@NgModule({
    imports: [
      RouterModule.forRoot(
        appRoutes
      )
    ],
    exports: [
      RouterModule
    ]
  })
export class AppRouters {}


