import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-proxy-ippool',
  templateUrl: './proxy-ippool.component.html',
  styleUrls: ['./proxy-ippool.component.css']
})
export class ProxyIppoolComponent implements OnInit {
  api_url = '';
  data = [];

  constructor(private http: HttpClient) { }

  ngOnInit() {
    this.getList()
  }

  search():void {
    this.getList();
  }

  getList():void {
    this.http
      .get(this.api_url + '/api/proxy/ippool/get-list/')
      .subscribe(
          data => {
            if (data['c'] == 0) {
              this.data = data['d']['data'];
            }
          },
          err => {
              alert('查询失败，请重试');
          }
      );
  }

}



