import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  api_url = '';

  constructor() { }

  ngOnInit() {
  }

  _logout() {
    location.href = this.api_url + '/api/auth/logout/';
  }

}



