import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  FormControl,
  Validators
} from '@angular/forms';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-proxy-user',
  templateUrl: './proxy-user.component.html',
  styleUrls: ['./proxy-user.component.css']
})
export class ProxyUserComponent implements OnInit {
  api_url = '';
  queryParams = {}
  data = [];

  searchForm: FormGroup;

  constructor(private fb: FormBuilder,
              private http: HttpClient)
  {

  }

  ngOnInit() {
    this.searchForm = this.fb.group({});
    this.searchForm.addControl(`email`, new FormControl());

    this.getList()
  }

  /**这里的账号列表相关 */
  getList():void {
    let params = this.queryParams;
    let query =  Object.keys(params).map(function(key) {
      if (params[key] !== null) {
        return key + '=' + params[key];
      }
    }).join('&');
    console.log(query);
    this.http
      .get(this.api_url + '/api/proxy/user/get-list/?'+ query)
      .subscribe(
          data => {
            if (data['c'] == 0) {
              this.data = data['d']['data'];
            }
          },
          err => {
              alert('查询失败，请重试');
          }
      );
  }

  search():void {
    for (const i in this.searchForm.controls) {
      this.queryParams[i] = this.searchForm.controls[ i ].value;
    }
    this.getList();
  }

  resetForm():void {
    this.searchForm.reset();
    this.queryParams = {};
  }


}



